*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

body{
  background: #2d3436;
}

.warp{
  width: 100%;
  height: 30vh;
  background: #2d3436;
  display: flex;
  justify-content: center;
  align-items: center
};

.search{
  width: 200ppx;
  height: 40px;                 
}

.searchTerm{
  width: 80%;
  height: auto;
  outline: none;
  border: none;
  padding: 5px 0px 5px 20px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  font-size: 15px;
}

.searchButton{
  width: 20%;
  height: auto;
  outline: none;
  border: none;
  text-align: center;
  padding: 5px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 15px;
  background: #00cec9;
}

.widthget{
  width: 100%;
  height: 40vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.weather{
  width: 30%;
  height: 50vh;
  background:#fff;
  border-radius: 20px;
  box-shadow: 0px 5px 20px 10px rgba(0, 0, 0, 0.5);
}

.weatherConditionIcon{
 display: flex;
 justify-content: center;
}

#weatherIcon{
  font-size: 80px;
  color: #00cec9;
  text-align: center;
  margin-top: 25px;
}

.weatherInfo{
 display: flex;
 overflow: hidden;
 margin-top: 20px;
}

.weatherInfoOne{
  display: flex;
  width: 70%;
  height: 100px;
  background: #000;
  color: #fff;
  text-align: center;
  padding: 10px 10px;
}

.countryInfoOne{
  display: flex;
  align-items: center;
}

.countryInfoTwo{
  margin-left: 40px;
  margin-top: 15px;
}

.weatherInfoTwo{
  width: 30%;
  height: 100px;
  background: rgb(87, 224, 183);
  color: #fff;
  text-align: center;
  padding: 10px 0px;
  font-size: 20px;
  padding: 15px;
}

.footerSection{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.footerInfo{
  display: flex;
  margin-left: 10px;
  margin-right: 10px;
}

.footerInfo:nth-child(1){
  margin-left: 0;
}

.footerIcon{
  padding: 10px 10px;
  color: #00cec9;
}
